const createShip = function (length, hits, isSunk) {

    return {
        length: length,
        hits: hits,
        isSunk: isSunk,
        hit: function() {
            this.hits += 1
            console.log("Updated hits:", this.hits);
            return this;
        }
    }
}

var redBox = document.createElement('div');
    
    // Set the style attributes of the red box
    redBox.style.width = '100px';
    redBox.style.height = '100px';
    redBox.style.backgroundColor = 'red';

    // Append the red box to the body of the document
    document.body.appendChild(redBox);

console.log('works');
const ship = createShip(4, 2, false);
ship.hit();
module.exports = createShip